// -----------------------------------------React-----------------------------------------
import React, { useState } from 'react';
// -----------------------------------------Reactstrap-----------------------------------------
import {
	Container,
	Row,
	Col,
	Button,
	Input,
	Modal,
	ModalBody,
	ModalHeader,
	ModalFooter,
	Form,
	FormGroup,
	InputGroup,
	ButtonGroup,
	InputGroupAddon,
	Label
} from 'reactstrap';
// -----------------------------------------React Hooks-----------------------------------------
import Context from '../store/context';
const AddPostModal = ({ isOpen, toggle }) => {
	let [] = useState({
		title: '',
		technologies: [],
		description: '',
		images: [],
		repo: '',
		codePen: ''
	});
	return (
		<Modal isOpen={isOpen} toggle={toggle}>
			<ModalHeader toggle={toggle}>Add a featured post</ModalHeader>
			<ModalBody>
				<Container>
					<Row>
						<Col>
							<ButtonGroup className='w-100'>
								<Button>Create Front End Post</Button>
								<Button>Create Project Post</Button>
							</ButtonGroup>
						</Col>
					</Row>
					<Row>
						<Col>
							<Label>Title</Label>
							<Input
								type='text'
								name='title'
								placeholder='Post Title'
							/>
						</Col>
					</Row>
					<Row>
						<Col>
							<Label>Description</Label>
							<Input
								type='textarea'
								name='description'
								placeholder='Post Description'
							/>
						</Col>
					</Row>
					<Row>
						<Col>
							<Label>Repo</Label>
							<Input
								type='text'
								name='repo'
								placeholder='Repository URL'
							/>
						</Col>
						<Col>
							<Label>CodePen</Label>
							<Input
								type='text'
								name='codepen'
								placeholder='CodePen URL'
							/>
						</Col>
					</Row>
					<Row>
						<Col>
							<Label>Technologies</Label>
							<div id='technology-output'>
								<span className='technology'>
									React<span className='close'>x</span>
								</span>
							</div>
							<InputGroup>
								<Input
									type='text'
									name='technologies'
									placeholder='React, Bootstrap, Css, Javascript...'
									className='mt-2'
								/>
								<Button>Add Technology</Button>
							</InputGroup>
						</Col>
					</Row>

					<Row>
						<Col>
							<Label>Add Images</Label>
							<Input type='file' name='images' />
						</Col>
					</Row>
				</Container>
			</ModalBody>
		</Modal>
	);
};
export default AddPostModal;
